
import {getTask} from '../../service/service.js'
import Search from 'vux/src/components/search/index.vue'

export default{
data(){
return{
show:false,
listArr:[],
list:[],
value:""
}
},
components: {
Search
},
methods:{
goDetail(param){
this.$router.push({path:"/Rose/to_do/do_detail",query:{
data:param
}})
},
setFocus () {
this.$refs.search.setFocus()
},
resultClick (item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult (val) {


},
onSubmit (value) {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
console.log(value);
},
onFocus () {
console.log('on focus')
},
onCancel () {
console.log('on cancel')
}
},
mounted(){
var self = this;
self.mescroll = new MeScroll("td",{
up:{
isBounce:false,
use:false

},
down:{
use:false
}

})
},
created(){
var token = localStorage.getItem("token");
getTask(token).then((res)=>{
console.log(res);
this.listArr = res.data.tableContent;
this.$event.$emit("num",res.data.tableContent.length);

})


}


}





