
import { getLogin,getDoneTask } from '../../service/service.js'
import Search from 'vux/src/components/search/index.vue'

export default{
data(){
return{
doneList:[],
mescroll:null,
show:true,
list:[],
value:""

}
},
components: {
Search
},
methods:{
goDetail(param){
this.$router.push({path:"/Rose/finish/done_detail",query:{
data:param
}})
},
setFocus () {
this.$refs.search.setFocus()
},
resultClick (item) {
window.alert('you click the result item: ' + JSON.stringify(item))
},
getResult (val) {


},
onSubmit (value) {
this.$refs.search.setBlur()
this.$vux.toast.show({
type: 'text',
position: 'top',
text: 'on submit'
})
console.log(value);
},
onFocus () {
console.log('on focus')
},
onCancel () {
console.log('on cancel')
}

},
created(){
var token = localStorage.getItem("token");
getDoneTask(token).then((res)=>{

this.doneList = res.tableContent;
console.log(res);
})

},
mounted(){
var self = this;
self.mescroll = new MeScroll("finish",{
up:{
isBounce:false,
use:false





},
down:{
use:false
}
})
}

}





